@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP&family=Roboto:wght@300&display=swap);
*{
    margin: 0px;
    padding: 0px;

}
html {
  scroll-behavior: smooth;
}

:root{
  --text-link-color: #000000; 
  --underline-link-color: #3c3096dc;
  --sendMessageBtn-hoverColor: #ffffff;
}

@media screen and (max-width: 500px) {
.nav{
    list-style-type: none;
    display: flex;
    width: 100vw;
    justify-content: center;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    padding: 0.5rem;
    padding: 1rem 0rem;
    position: fixed;
    margin: 0px;
    transition-timing-function: ease-in;
    transition: 0.5s;
    background-color: white;
}
.sendMessage{
  color: #3c3096dc;
  color: var(--underline-link-color);
  border: 2px solid #3c3096dc;
  border: 2px solid var(--underline-link-color);
  border-radius: 30px;
  padding: 3.6px;
  font-family: 'Roboto', sans-serif;
  margin-top: 1rem;
  font-weight: 700;
  font-size: 1.2rem;
}


.sendMessage:link{
  color: #000000;
  color: var(--text-link-color);
  text-decoration: none;
  font-weight: 700;
}

.sendMessage:visited{
  color: #000000;
  color: var(--text-link-color);
  text-decoration: none;
  font-weight: 700;
}

.sendMessage:hover{
  color: #ffffff;
  color: var(--sendMessageBtn-hoverColor);
  background-color: #3c3096dc;
  background-color: var(--underline-link-color);
  transition: 0.3s;
  font-weight: 700;
}

.link{
  font-family: 'Roboto', sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
}

.link:link{
    color: #000000;
    color: var(--text-link-color);
    text-decoration: none;
    font-weight: 700;
}


 .link:visited {
  border-bottom: 3px solid #3c3096dc;
  border-bottom: 3px solid var(--underline-link-color);
  color: #000000;
  color: var(--text-link-color);

  }
  
  .link:hover {
    border-bottom: 3px solid #3c3096dc;
    border-bottom: 3px solid var(--underline-link-color);
    color: #000000;
    color: var(--text-link-color);
  }
  
  .link:active {
    border-bottom: 3px solid #3c3096dc;
    border-bottom: 3px solid var(--underline-link-color);
    color: #000000;
    color: var(--text-link-color);
  }
}

